<template>
  <div class="index">
    <!-- For Desktop -->
    <div v-responsive.lg.xl class="header">
      <d-header />
    </div>
    <div v-responsive.md class="theader">
      <t-header />
    </div>
    <div v-responsive.sm.xs class="theader">
      <t-header />
    </div>
    <!-- Slide -->
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      indicators
      background="#ababab"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      class="car"
    >
      <!-- Text slides with image -->
      <b-carousel-slide
        class="slide"
        caption="First Slide"
        text="Nulla vitae elit libero, a pharetra augue mollis interdum. "
      >
        <img slot="img" width="100%" height="400px" src="@/assets/images/1.jpg" />
      </b-carousel-slide>
      <!-- Slides width custum text -->
      <b-carousel-slide>
        <img slot="img" width="100%" height="400px" src="@/assets/images/2.jpg" />
        <h1>Hello World</h1>
      </b-carousel-slide>
      <!-- Slides width image only -->
      <b-carousel-slide>
        <img slot="img" width="100%" height="400px" src="@/assets/images/3.jpg" />
      </b-carousel-slide>
      <!-- Slides width img slot -->
      <b-carousel-slide>
        <img slot="img" width="100%" height="400px" src="@/assets/images/4.jpg" alt="image slot" />
      </b-carousel-slide>
    </b-carousel>
    <main-content />
    <t-footer />
  </div>
</template>
<script>
import { TFooter, DHeader, THeader, MainContent } from "./components";
import ResizeMixin from "./mixin/ResizeHandler";
export default {
  name: "Layout",
  components: {
    TFooter,
    DHeader,
    THeader,
    MainContent
  },
  mixins: [ResizeMixin],
  data() {
    return {
      slide: 0,
      sliding: null
    };
  },
  methods: {
    onSlideStart() {
      this.sliding = true;
    },
    onSlideEnd() {
      this.sliding = false;
    }
  }
};
</script>
<style >
.index {
  position: absolute;
  width: 100%;
  height: 100%;
}

.slide {
  width: 100%;
  height: 50%;
  object-fit: cover;
}
.car {
  height: 400px;
  object-fit: cover;
}
.bodycard {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 20%;
  background-color: rgb(92, 92, 112);
}
.main-container {
  background-color: white;
}
.img-responsive {
  width: 100%;
  margin-top: 1rem;
}
.container-fluid {
  padding-left: 1rem;
  padding-right: 1rem;
}
.special-heading-a {
  max-width: 70rem;
  margin: auto;
  margin-top: 4rem;
}

.special-heading-b {
  max-width: 60rem;
  margin: auto;
}
.special-heading-a-text,
.special-heading-b-text,
.special-heading-c-text,
.special-heading-f-text {
  line-height: 2rem;
  font-weight: 200;
  margin-top: 1rem;
}
.fonts4,
.head5 {
  font-size: 1.2rem;
}

.the-fieldset {
  /* padding: 1.0rem; */
  margin-top: 1.5rem;
  border: 0rem;
  width: 100%;
}
.col-sm-6 {
  margin-bottom: 1rem;
}

.rcom-mb {
  margin-bottom: 1.6rem;
}
.holidays-2018-2 .col-sm-3 {
  margin-bottom: 2rem;
}
.holidays-2018-2-img-bottom {
  margin-bottom: 1rem;
}

.holidays-2018-2 .p-names {
  height: 6rem;
  margin-top: -6rem;
}
.holidays-2018-2 .p-names span {
  display: block;
  /* font-size: 1rem; */
  line-height: 1.168;
  font-weight: normal;
}
a {
  cursor: pointer;
  text-decoration: none !important;
  /* font-size: 1.3rem;
    line-height: 1.618; */
  outline: 0rem;
}


#productBannerTitle {
  position: relative;
  height: 15rem;
  background-image: url(../../assets/images/hdecor.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  border-radius: 0.5rem;
}
#productBannerTitle .product-banner-title-content {
  position: absolute;
  top: 5rem;
  left: 10%;
}
#productBannerTitle .product-banner-title-item-link {
  margin-top: 0.1rem;
  display: inline-block;
}
#productBannerTitle .p-names-price {
  position: relative;
  display: block;
  color: #484848;
  font-size: 1.1rem;
}

.cds {
  position: relative;
  margin-top: 2rem;
  max-width: 100%;
  margin: auto;
  /* overflow: hidden; */
}
.cds img {
  display: block;
  max-width: 100%;
  height: auto;
  /* min-width: 70.0rem; */
}
.cds-text {
  position: absolute;
  top: 13%;
  left: 50%;
  padding: 2.5rem;
  background-color: rgba(255, 255, 255, 0.75);
}
.cds-text p {
  margin-top: 1.5%;
}
.cds-text h3,
.cds-text p {
  font-size: 1.3rem;
  line-height: 1.5rem;
  font-weight: 200;
}
.cds-text .button {
  margin-top: 1rem;
  margin-bottom: 0rem;
  font-size: 1rem;
  padding: 0.7rem 2rem;
}

.button {
  padding: 0.8rem 2rem;
  border-radius: 10rem;
  border: 0rem;
  /* font-size: 1.6rem; */
  font-weight: bold;
  line-height: 1rem;
  font-family: "Open Sans", Arial, sans-serif, Helvetica !important;
  outline: none;
  transition: all 0.3s ease 0s;
  text-transform: capitalize;
}
.button {
  background-color: #3e84b6;
  color: #fff;
  box-shadow: 0rem 0.1rem 0rem #132836;
}

.cds-texttablet {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.75);
  height: 9rem;
  width: 100%;
  bottom: 0;
  top: auto !important;
  left: 0;
  margin: 0;
  padding: 0.4rem 0.1rem;
  text-align: center;
}

.cds-texttablet h2,
.cds-texttablet h3 {
  margin: 0;
  text-align: center;
}
.cds-texttablet h2 {
  line-height: 3rem;
  font-weight: bold;
  font-size: 1.8rem;
}
.cds-texttablet h3,
.cds-texttablet p {
  font-size: 1.2rem;
  line-height: 1rem;
  font-weight: 300;
}
.cds-texttablet p {
  display: none;
}
.cds-texttablet .button {
  margin-top: 1rem;
}

.guarantee-block {
  margin: 5rem 0 5rem 0rem;
}

.promotional-svg {
  height: 3.5rem;
  margin-right: 2rem;
  margin-top: -1rem;
}
.fl {
  float: left;
}
.col-sm-6 p {
  font-size: 0.9rem;
  line-height: 1.3rem;
  text-align: justify;
}
.col-sm-6 h4 {
  font-size: 1.2rem;
  font-weight: bold;
  color: #484848;
  margin-bottom: 0.5rem;
  line-height: 2.6rem;
}
.para {
  font-size: 0.85rem;
  padding: 1rem 0;
  line-height: 1.6rem;
}
.transparent-class {
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  filter: alpha(opacity=50);
  -moz-opacity: 0.5;
  -khtml-opacity: 0.5;
  opacity: 0.5;
  height: 2rem;
  overflow: hidden;
  display: block;
}
</style>



