  methods: {
    onReadMore() {
      let more = document.querySelector(".read-more-cyber");
      let moredata = document.querySelector(".holidayAccordion");
      let tp = document.querySelector(".transparent-class");
      if (more.classList.contains("active")) {
        more.classList.remove("active");
        this.readless.display = "none";
        this.moretext = "Read More";
        this.arrowicon = "fa fa-angle-down";
        tp.style.opacity = "0.5";
        tp.style.height = "2rem";
      } else {
        more.classList.add("active");
        this.readless.display = "block";
        this.moretext = "Read Less";
        this.arrowicon = "fa fa-angle-up";

        tp.style.opacity = 1;
        tp.style.height = "auto";
      }
    }
